<template>
    <div class="listtemplates">
        <ul>
            <li v-for="(item,index) in listData" @click="saveHistory(item)">
                <router-link :to="{name: 'detail' , params:{id: item.code}}" class="dt">
                    <img class="car" v-lazy="item.firstImage" v-if="item.firstImage != null && item.firstImage != ''" />
                    <img class="car" src="../../../assets/noImg.jpg" alt="" v-else>
                    <img src="../../../assets/choice.png" class="choice" alt="">
                </router-link>
                <div class="car_mess">
                    <div class="pub_height">
                        <router-link :to="{name: 'detail' , params:{id: item.code}}">
                            <p class="car_name">{{item.title}}</p>
                        </router-link>
                        <p class="car_time">
                            <template v-if="item.firstRegisterDate">
                                <span class="listTime">{{item.firstRegisterDate | listTime}}</span>
                            </template>
                            <template v-if="item.firstRegisterDate && item.mileage">
                                /
                            </template>
                            <template v-if="item.mileage">
                                <span class="mileage">{{item.mileage}} 万公里</span>
                            </template>
                            <template v-if="item.mileage && item.exhibitionArea">
                                /
                            </template>
                            <template v-if="item.exhibitionArea">
                                /{{item.exhibitionArea}}区{{item.exhibitionCode}}
                            </template>
                        </p>
                    </div>

                    <div class="price_border">
                        <span class="show_price">{{item.originalPrice | tofixed}}万</span>
                        <span v-for="tag in item.tags" class="tag" :class="{hand : tag == '练手', suv : tag == 'SUV', beNew : tag == '准新', sold : tag == '急售', jx : tag == '精选', tj : tag == '推荐'}"></span>
                        <template v-if="item.isInLook == 0">
                            <span class="to_look" @click="addCarList(item)"></span>
                        </template>
                        <template v-else="item.isInLook == 1">
                            <span class="looked" @click="removeCarList(item)"></span>
                        </template>
                    </div>
                </div>
            </li>
        </ul>
    </div>
</template>

<style lang="scss" scoped>
image[lazy="loading"] {
    width: 2.2rem;
    height: 1.64rem;
}
.listtemplates {
    width: 100%;
    height: auto;
    /*border-bottom: 0.6rem solid #fff;*/
    .loading {
        margin-top: 40%;
    }
    ul {
        margin-top: 0.3rem;
        li {
            display: flex;
            box-sizing: border-box;
            border-bottom: 1px solid #f2f2f2;
            padding-bottom: 0.2rem;
            margin: 0 0.2rem;
            &:not(:first-child) {
                margin-top: 0.3rem;
            }
            .car {
                width: 2.2rem;
                height: 1.64rem;
            }
            .dt {
                position: relative;
                .choice {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 0.6rem;
                    height: 0.6rem;
                }
            }
            .car_mess {
                margin-left: 0.22rem;
                height: 1.64rem;
                position: relative;
                a {
                    text-decoration: none;
                    display: block;
                }
                .pub_height {
                    height: 1.26rem;
                }
                .car_name {
                    color: #222;
                    width: 4.54rem;
                    font-size: 0.26rem;
                    line-height: 0.4rem;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                }
                .car_time {
                    margin: 0.08rem 0 0.16rem 0;
                    height: 0.3rem;
                    span {
                        font-size: 0.22rem;
                        color: #666;
                    }
                }
                .price_border {
                    display: flex;
                    line-height: 1;
                    margin-top: 0.1rem;
                    span:not(:first-child) {
                        margin-left: 0.1rem;
                    }
                    .tag {
                        display: inline-block;
                        font-size: 0.22rem;
                        border-radius: 0.04rem;
                        width: 0.7rem;
                        height: 0.28rem;
                        line-height: 0.28rem;
                        text-align: center;
                        &.hand {
                            background: url(../../../assets/hand.png) center
                                no-repeat;
                            background-size: 100% 100%;
                        }
                        &.beNew {
                            background: url(../../../assets/beNew.png) center
                                no-repeat;
                            background-size: 100% 100%;
                        }
                        &.suv {
                            background: url(../../../assets/SUV.png) center
                                no-repeat;
                            background-size: 100% 100%;
                        }
                        &.jx {
                            display: none;
                        }
                        &.sold {
                            background: url(../../../assets/soldSpe.png) center
                                no-repeat;
                            background-size: 100% 100%;
                        }
                        &.tj {
                            background: url(../../../assets/recommd.png) center
                                no-repeat;
                            background-size: 100% 100%;
                        }
                    }
                    .show_price {
                        float: left;
                        font-size: 0.3rem;
                        color: #ff0000;
                    }
                    .to_look {
                        position: absolute;
                        right: 0;
                        bottom: 0;
                        width: 0.44rem;
                        height: 0.5rem;
                        background: url("../../../assets/love.png") no-repeat;
                        background-size: 0.38rem;
                    }
                    .looked {
                        position: absolute;
                        right: 0;
                        bottom: 0;
                        width: 0.44rem;
                        height: 0.5rem;
                        background: url("../../../assets/loved.png") no-repeat;
                        background-size: 0.38rem;
                    }
                }
            }
        }
    }
}
</style>

<script>
// import vueLoading from 'vue-loading-template';
import { Toast, Lazyload } from "mint-ui";
import Vue from "vue";
export default {
    props: {
        listData: {}
    },
    data() {
        return {
            // isLoading: true,
            s: [],
            customerId: "",
            tokens: "",
            isiOS: "",
            nativeHref: ""
        };
    },
    mounted() {
        var _this = this;
        if (window.localStorage.getItem("user")) {
            if (window.localStorage.getItem("item")) {
                _this.addCarList(
                    JSON.parse(window.localStorage.getItem("item"))
                );
                window.localStorage.setItem("item", "");
            }
        }

        if (window.localStorage.getItem("user")) {
            this.customerId = JSON.parse(
                window.localStorage.getItem("user")
            ).customer;
            this.tokens = JSON.parse(window.localStorage.getItem("user")).token;
        }
    },
    components: {
        //   vueLoading,
    },
    methods: {
        loadMores() {
            this.loading = true;
            setTimeout(() => {
                // let last = this.list[this.list.length - 1];
                // for (let i = 1; i <= 10; i++) {
                //   this.list.push(last + i);
                // }
                this.loading = false;
            }, 2500);
        },
        addCarList: function(item) {
            var _this = this;
            var storage = window.localStorage;
            if (storage.getItem("user")) {
                $.ajax({
                    type: "post",
                    url: _this.global.host + "/watchList/add",
                    data: {
                        token: JSON.parse(window.localStorage.getItem("user"))
                            .token,
                        vehicleId: item.code
                    },
                    success: function(response) {
                        if (response.resultCode == "0000") {
                            item.isInLook = 1;
                            Toast("加入看车单成功");
                        } else if (response.resultCode == "1111") {
                            Toast("加入看车单失败");
                        }
                    }
                });
            } else {
                window.localStorage.setItem("item", JSON.stringify(item));
                _this.$router.push("/login");
            }
        },
        removeCarList(item) {
            var _this = this;
            $.ajax({
                type: "POST",
                url: this.global.host + "watchList/remove",
                data: {
                    customerId: _this.customerId,
                    token: _this.tokens,
                    vehicleId: item.code
                },
                success: function(response) {
                    if (response.resultCode === "0000") {
                        Toast("取消加入看车单");
                        item.isInLook = 0;
                    } else if (response.resultCode === "1111") {
                        Toast("操作失败");
                    }
                }
            });
        },
        saveHistory(item) {
            var Storage = window.localStorage;
            var ifHas = Storage.getItem("History");
            if (ifHas) {
                var newStorage = JSON.parse(ifHas);
                if (newStorage.length == 50) {
                    newStorage.pop();
                    newStorage.unshift(item);
                    var arrTable = {},
                        arrData = [];
                    for (var i = 0; i < newStorage.length; i++) {
                        if (!arrTable[newStorage[i].code]) {
                            arrTable[newStorage[i].code] = true;
                            arrData.push(newStorage[i]);
                        }
                    }
                    Storage.setItem("History", JSON.stringify(arrData));
                } else {
                    newStorage.unshift(item);
                    var arrTable = {},
                        arrData = [];
                    for (var i = 0; i < newStorage.length; i++) {
                        if (!arrTable[newStorage[i].code]) {
                            arrTable[newStorage[i].code] = true;
                            arrData.push(newStorage[i]);
                        }
                    }
                    Storage.setItem("History", JSON.stringify(arrData));
                }
            } else {
                this.s.unshift(item);
                Storage.setItem("History", JSON.stringify(this.s));
            }
        }
    }
};
Vue.filter("listTime", function(value) {
    //value为13位的时间戳
    function addt(m) {
        return m < 10 ? "0" + m : m;
    }
    var time = new Date(parseInt(value));
    var y = time.getFullYear();
    var m = time.getMonth() + 1;
    var d = time.getDate();

    return y + "年" + addt(m) + "月";
});
</script>



