				<span class="menu" @click="showMenu('搜索')"><img src="../../../assets/menu.png" width="100%" alt=""></span>
			</div>
		</div>
		<div class="listContent">
			<ul class="menuContent">
				<li @click="sort" :class="{ active: vxe.r_sort }">
					<template v-if="selectShow.sortShow != ''">
						<p class="sortBold" v-for="(item,index) in selectShow.sortShow">{{item.name}}</p>
					</template>
