						<i class="icon iconfont icon-jiankuohaoxixia"></i>
					</template>
				</li>
				<li id="r_brand" @click="brand" :class="{ active: vxe.r_brand }">
					<p class="brandBold">品牌</p>
					<template v-if="vxe.r_brand">
						<i class="icon iconfont icon-jiankuohaoxishang"></i>
					</template>
					<template v-else>
						<i class="icon iconfont icon-jiankuohaoxixia"></i>
					</template>
				</li>
				<li id="r_price" @click="price" :class="{ active: vxe.r_price }">
					<p class="priceBold">价位</p>
					<template v-if="vxe.r_price">
						<i class="icon iconfont icon-jiankuohaoxishang"></i>
