					<template v-else>
						<i class="icon iconfont icon-jiankuohaoxixia"></i>
					</template>
				</li>
				<li id="r_more" @click="more" :class="{ active: vxe.r_more }">
					<p class="moreBold">筛选</p>
					<template v-if="vxe.r_more">
						<i class="icon iconfont icon-jiankuohaoxishang"></i>
