				</li>
				<li @click="dy">
					<span class="subscribe">订阅</span>
				</li>
			</ul>

			<div class="clearfix" v-if="isShowSomeLable" style="background:#f6f6f6;position:relative;overflow:hidden;">
				<div class="swiper-container s_option">
					<ul class="swiper-wrapper op_left">
						<li class="swiper-slide" v-for="(item,index) in selectShow.brandNameShow">
							<span>{{item.brandGroupName}}</span>
							<span @click="deBrandId(index)"></span>
						</li>
						<li class="swiper-slide" v-for="(item,index) in selectShow.brandShow">
							<span>{{item.name}}</span>
							<span @click="deBrand(index)"></span>
						</li>
						<li class="swiper-slide" v-for="(item,index) in selectShow.priceShow">
							<span>{{item.name}}</span>
							<span @click="dePrice(index)"></span>
						</li>
						<li class="swiper-slide" v-for="(item,index) in selectShow.moreShow">
							<span>{{item.name}}</span>
							<span @click="deMore(index)"></span>
						</li>
						<template v-if="selectShow.seachShow.name">
							<li class="swiper-slide">
								<span>{{selectShow.seachShow.name}}</span>
								<span @click="deSeach()"></span>
							</li>
						</template>
					</ul>
				</div>
				<div class="op_clear" @click="removeLable">清空</div>
			</div>

			<mt-loadmore :top-method="loadTop" @top-status-change="handleTopChange" :bottom-method="loadMore" @bottom-status-change="handleBottomChange" :bottom-all-loaded="allLoaded" :autoFill="false" ref="loadmore">

				<keep-alive>
					<v-listtemplate :listData="listData" v-if="ismenuToHide"></v-listtemplate>
				</keep-alive>
				<div class="noData" v-if="isNoData">
					<img src="./noData.png" alt="">
					<p>该条件下暂无车源，更换筛选条件试一下吧～</p>
				</div>
				<div slot="top" class="mint-loadmore-top">
					<span v-show="topStatus === 'loading'">
						正在加载...
					</span>
					<span v-show="topStatus !== 'loading'" :class="{ 'rotate': topStatus === 'drop' }">松开可以刷新...</span>

				</div>
				<div slot="bottom" class="mint-loadmore-bottom">
					<span v-show="bottomStatus === 'loading'">
						更多车辆加载中...
					</span>
				</div>
			</mt-loadmore>
			<transition>
				<v-brand v-if="vxe.r_brand" @brandClickEvent="brandClickEvent" @hideBrand="hideBrand" @clearBrand="clearBrand"></v-brand>
			</transition>
			<transition>
				<v-price v-if="vxe.r_price" @priceClickEvent="priceClickEvent" @hidePrice="hidePrice"></v-price>
			</transition>
			<transition>
				<v-more v-if="vxe.r_more" @moreClickEvent="moreClickEvent" @hideMore="hideMore" :pTocMore="pTocMore"></v-more>
			</transition>
			<transition>
				<v-sort v-if="vxe.r_sort" @sortClickEvent="sortClickEvent" @hideSort="hideSort"></v-sort>
			</transition>
		</div>
		<v-menuPage ref="menuMark" @close="closeThis"></v-menuPage>
		<div class="gotop" @click="scrolltop"></div>
	</div>
</template>

