<script>
import recommend from "../listrecommend/listrecommend.vue";
import listtemplate from "../listtemplate/listtemplate.vue";
import brand from "../brand/brand.vue";
import more from "../more/more.vue";
import price from "../price/price.vue";
import sort from "../sort/sort.vue";
import menuPage from "../../public/nav.vue";
import { Toast,Indicator,Loadmore } from 'mint-ui';

export default {
    data() {
        return {
            vxe: {
                r_brand: false,
                r_price: false,
                r_more: false,
                r_sort: false
            },
            nativeCity: "",
            nativeToken: "",
            isShowRecommendData: false,
            isNoData: false,
            isShowSomeLable: false,
            ismenuToHide: true,
            recommendData: {},
            listData: [],
            selectShow: {
                brandNameShow: [],
                brandShow: [],
                priceShow: [],
                sortShow: [],
                moreShow: [],
                seachShow: [],
                hotShow: [],
                brandGroupIds: "",
                theSum: {}
            },
            pTocMore: {},
            pageNum: 1,
            pageCount: 10,
            isShowLoadMore: false,
            showCirle: false,
            search_othor_type: false,
            exitIds: [],
            min_price: "",
            max_price: "",
            topStatus: "",
            bottomStatus: "",
            allLoaded: false,
            tag: "",
            getList: {},
            historySea: {}
        };
    },
    methods: {
        //订阅
        dy(){
			Toast('改功能暂未上线，敬请期待！')
		},
        handleTopChange(status) {
            this.topStatus = status;
        },
        handleBottomChange(status) {
            this.bottomStatus = status;
        },
        loadTop() {
            setTimeout(() => {
                this.getSearchList(this.getList);
            }, 1500);
        },
        back() {
            this.$router.back(-1);
        },
        showMenu(msg) {
            this.$refs.menuMark.show(msg, $(".list"));
        },
        closeThis() {
            this.showThis = false;
        },
        brand() {
            this.vxe.r_brand = !this.vxe.r_brand;
            this.vxe.r_price = false;
            this.vxe.r_more = false;
            this.vxe.r_sort = false;
            if (
                this.vxe.r_brand == true ||
                this.vxe.r_price == true ||
                this.vxe.r_more == true ||
                this.vxe.r_sort == true
            ) {
                this.ismenuToHide = false;
                this.isShowRecommendData = false;
            } else {
                this.ismenuToHide = true;
                this.isShowRecommendData = true;
            }
        },
        price() {
            this.vxe.r_price = !this.vxe.r_price;
            this.vxe.r_brand = false;
            this.vxe.r_more = false;
            this.vxe.r_sort = false;
            if (
                this.vxe.r_brand == true ||
                this.vxe.r_price == true ||
                this.vxe.r_more == true ||
                this.vxe.r_sort == true
            ) {
                this.ismenuToHide = false;
                this.isShowRecommendData = false;
            } else {
                this.ismenuToHide = true;
                this.isShowRecommendData = true;
            }
        },
        more() {
            this.vxe.r_more = !this.vxe.r_more;
            this.vxe.r_price = false;
            this.vxe.r_brand = false;
            this.vxe.r_sort = false;
            if (
                this.vxe.r_brand == true ||
                this.vxe.r_price == true ||
                this.vxe.r_more == true ||
                this.vxe.r_sort == true
            ) {
                this.ismenuToHide = false;
                this.isShowRecommendData = false;
            } else {
                this.ismenuToHide = true;
                this.isShowRecommendData = true;
            }
        },
        sort() {
            this.vxe.r_sort = !this.vxe.r_sort;
            this.vxe.r_more = false;
            this.vxe.r_price = false;
            this.vxe.r_brand = false;
            if (
                this.vxe.r_brand == true ||
                this.vxe.r_price == true ||
                this.vxe.r_more == true ||
                this.vxe.r_sort == true
            ) {
                this.ismenuToHide = false;
                this.isShowRecommendData = false;
            } else {
                this.ismenuToHide = true;
                this.isShowRecommendData = true;
            }
        },
        deBrand(index) {
            var _this = this,
                labelLength = $(".op_left li").length;

            _this.selectShow.brandShow.splice(index, 1);

            if (labelLength - 1 == 0) {
                _this.isShowSomeLable = false;
            }
            delete _this.selectShow.theSum.type_name;
            delete _this.selectShow.theSum.typeIds;
            this.getSearchList(_this.selectShow.theSum);
            this.getList = _this.selectShow.theSum;
        },
        deBrandId(index) {
            var _this = this;
            var labelLength = $(".op_left li").length;
            var thisTheSum = _this.selectShow.theSum;

            _this.selectShow.brandGroupIds = "";
            if (
                _this.selectShow.brandNameShow != "" &&
                _this.selectShow.brandShow != ""
            ) {
                _this.selectShow.brandNameShow.splice(index, 1);
                _this.selectShow.brandShow.splice(index, 1);
                if (labelLength - 2 == 0) {
                    _this.isShowSomeLable = false;
                }
            }
            _this.selectShow.brandNameShow.splice(index, 1);
            _this.selectShow.brandShow.splice(index, 1);
            if (labelLength - 1 == 0) {
                _this.isShowSomeLable = false;
            }
            if (_this.selectShow.brandShow.length == 0) {
                $(".brandBold").css("font-weight", "100");
            }

            if (thisTheSum.brandGroupName) {
                delete thisTheSum.brandGroupName;
            }
            if (thisTheSum.brandGroupIds) {
                delete thisTheSum.brandGroupIds;
            }

            if (thisTheSum.type_name) {
                delete thisTheSum.type_name;
            }
            if (thisTheSum.typeIds) {
                delete thisTheSum.typeIds;
            }
            _this.selectShow.theSum = thisTheSum;
            this.getSearchList(_this.selectShow.theSum);
            this.getList = _this.selectShow.theSum;
        },
        dePrice(index) {
            var _this = this,
                labelLength = $(".op_left li").length;
            _this.selectShow.priceShow.splice(index, 1);
            if (labelLength - 1 == 0) {
                _this.isShowSomeLable = false;
            }
            if (_this.selectShow.priceShow == 0) {
                $(".priceBold").css("font-weight", "100");
            }
            if (_this.selectShow.theSum.max_price) {
                delete _this.selectShow.theSum.max_price;
            }
            if (_this.selectShow.theSum.min_price) {
                delete _this.selectShow.theSum.min_price;
            }
            this.getSearchList(_this.selectShow.theSum);
            this.getList = _this.selectShow.theSum;
        },
        deMore(index) {
            var _this = this,
                labelLength = $(".op_left li").length;

            var thisTheSum = _this.selectShow.theSum;
            var thisTheHis = _this.historySea;
            if (_this.selectShow.moreShow[index].name == "准新车") {
                if (thisTheSum.isNearlyNewCar) {
                    delete thisTheSum.isNearlyNewCar;
                }
            }
            if (_this.selectShow.moreShow[index].name == "练手车") {
                if (thisTheSum.isPracticeCar) {
                    delete thisTheSum.isPracticeCar;
                }
            }

            if (
                _this.selectShow.moreShow[index].name == "微型车" ||
                _this.selectShow.moreShow[index].name == "小型车" ||
                _this.selectShow.moreShow[index].name == "紧凑型车" ||
                _this.selectShow.moreShow[index].name == "中型车" ||
                _this.selectShow.moreShow[index].name == "中大型车" ||
                _this.selectShow.moreShow[index].name == "跑车" ||
                _this.selectShow.moreShow[index].name == "SUV" ||
                _this.selectShow.moreShow[index].name == "MPV" ||
                _this.selectShow.moreShow[index].name == "豪华车" ||
                _this.selectShow.moreShow[index].name == "面包车" ||
                _this.selectShow.moreShow[index].name == "皮卡" ||
                _this.selectShow.moreShow[index].name == "客车"
            ) {
                var carName = _this.selectShow.moreShow[index].name;
                if (thisTheSum.typeLevels.indexOf(carName) >= 0) {
                    var ostr = thisTheSum.typeLevels;
                    ostr = ostr.split(",");
                    ostr = ostr.filter(t => t !== carName);
                    ostr = ostr.join(",");
                    thisTheSum.typeLevels = ostr;
                }
            }

            if (_this.selectShow.moreShow[index].name.indexOf("年") >= 0) {
                delete this.selectShow.theSum.ageRange;
            }

            if (
                _this.selectShow.moreShow[index].name == "手动" ||
                _this.selectShow.moreShow[index].name == "自动"
            ) {
                var carName = _this.selectShow.moreShow[index].name;
                if (thisTheSum.gearboxes.indexOf(carName) >= 0) {
                    var ostr = thisTheSum.gearboxes;
                    ostr = ostr.split(",");
                    ostr = ostr.map(x => (x === carName ? "" : x));
                    ostr = ostr.filter(t => t !== "");
                    ostr = ostr.join(",");
                    thisTheSum.gearboxes = ostr;
                }
            }

            if (_this.selectShow.moreShow[index].name.indexOf("万公里") >= 0) {
                delete this.selectShow.theSum.mileageRange;
            }

            if (_this.selectShow.moreShow[index].name.indexOf("升") >= 0) {
                delete this.selectShow.theSum.displacementRange;
            }

            if (
                _this.selectShow.moreShow[index].name == "国一" ||
                _this.selectShow.moreShow[index].name == "国二" ||
                _this.selectShow.moreShow[index].name == "国三" ||
                _this.selectShow.moreShow[index].name == "国四" ||
                _this.selectShow.moreShow[index].name == "国五"
            ) {
                var carName = _this.selectShow.moreShow[index].name;
                if (thisTheSum.emissionStandards.indexOf(carName) >= 0) {
                    var ostr = thisTheSum.emissionStandards;
                    ostr = ostr.split(",");
                    ostr = ostr.map(x => (x === carName ? "" : x));
                    ostr = ostr.filter(t => t !== "");
                    ostr = ostr.join(",");
                    thisTheSum.emissionStandards = ostr;
                }
            }
            if (
                _this.selectShow.moreShow[index].name == "2座" ||
                _this.selectShow.moreShow[index].name == "5座" ||
                _this.selectShow.moreShow[index].name == "5座以上"
            ) {
                var carName = _this.selectShow.moreShow[index].name.substring(
                    0,
                    1
                );
                if (thisTheSum.seats.indexOf(carName) > 0) {
                    var ostr = thisTheSum.seats;
                    ostr = ostr.split(",");
                    ostr = ostr.map(x => (x === carName ? "" : x));
                    ostr = ostr.filter(t => t !== "");
                    ostr = ostr.join(",");
                    thisTheSum.seats = ostr;
                }
            }
            if (
                _this.selectShow.moreShow[index].name == "前驱" ||
                _this.selectShow.moreShow[index].name == "后驱" ||
                _this.selectShow.moreShow[index].name == "四驱"
            ) {
                var carName = _this.selectShow.moreShow[index].name;
                if (thisTheSum.drives.indexOf(carName) >= 0) {
                    var ostr = thisTheSum.drives;
                    ostr = ostr.split(",");
                    ostr = ostr.map(x => (x === carName ? "" : x));
                    ostr = ostr.filter(t => t !== "");
                    ostr = ostr.join(",");
                    thisTheSum.drives = ostr;
                }
            }
            if (
                _this.selectShow.moreShow[index].name == "黑色" ||
                _this.selectShow.moreShow[index].name == "白色" ||
                _this.selectShow.moreShow[index].name == "灰色" ||
                _this.selectShow.moreShow[index].name == "红色" ||
                _this.selectShow.moreShow[index].name == "蓝色" ||
                _this.selectShow.moreShow[index].name == "金色" ||
                _this.selectShow.moreShow[index].name == "银色" ||
                _this.selectShow.moreShow[index].name == "黄色" ||
                _this.selectShow.moreShow[index].name == "紫色" ||
                _this.selectShow.moreShow[index].name == "橙色" ||
