        getSearchList(data) {
            let _this = this;
            $.ajax({
                type: "POST",
                url: this.global.host + "inventoryV2/queryVehicleList",
                data: data,
                success: function(response) {
                    if (response.resultCode == "0000") {
                        _this.$refs.loadmore.onTopLoaded();
                        _this.listData = response.result.resultList;
                        _this.recommendData = response.result.recommendList;
                        _this.isNoData = false;
                        if (_this.listData.length == 0) {
                            _this.isNoData = true;
                        }
                        if (_this.recommendData == 0) {
                            _this.isShowRecommendData = false;
                        }
                        _this.exitIds = [];
                        _this.listData.forEach(function(list) {
                            _this.exitIds.push(list.vehicle_id);
                        });
                        if (_this.listData.length < 10) {
                            if (_this.recommendData.length > 0) {
                                _this.isShowRecommendData = true;
                                _this.isShowLoadMore = false;
                            } else {
                                _this.isShowRecommendData = false;
                                _this.isShowLoadMore = true;
                            }
                        } else {
                            _this.isShowRecommendData = false;
                            _this.isShowLoadMore = true;
                        }
                    } else if (response.resultCode == "1111") {
                        _this.exitIds = [];
                        _this.isShowRecommendData = true;
                        _this.isShowLoadMore = false;
                        _this.listData = [];
                        _this.isNoData = true;
                    }
                },
                complete: function(XMLHttpRequest, status) {
                    if (status === "timeout") {
                    }
                },
                error: function() {
                    alert("网络连接失败！");
                }
            });
        },
        sitloadmore(allValue) {
