            if (_this.pageNum >= _this.pageCount) {
                $(".loadMore").html("加载完毕~！");
            } else {
                _this.showCirle = true;
                setTimeout(function() {
                    _this.showCirle = false;
                    $.ajax({
                        type: "POST",
                        url: _this.global.host + "inventoryV2/queryVehicleList",
                        data: allValue,
                        success: function(response) {
                            if (response.resultCode == "0000") {
                                _this.$refs.loadmore.onBottomLoaded();
                                _this.pageCount = response.result.resultList;
                                if (_this.getList.pageNum) {
                                    delete _this.getList.pageNum;
                                }
                                for (
                                    var i = 0;
                                    i < response.result.resultList.length;
                                    i++
                                ) {
                                    _this.listData.push(
                                        response.result.resultList[i]
                                    );
                                }
                                _this.isNoData = false;
                                if (_this.listData.length < 10) {
                                    _this.isShowRecommendData = true;
                                    _this.isShowLoadMore = false;
                                } else {
                                    _this.isShowRecommendData = false;
                                    _this.isShowLoadMore = true;
                                }
                            } else if (response.resultCode == "1111") {
                                _this.isShowRecommendData = true;
                                _this.isShowLoadMore = false;
                                _this.listData = [];
                                _this.isNoData = true;
                            }
                        }
                    });
                }, 1000);
            }
        },
