                _this.selectShow.priceShow = [{ name: max_price + "万以下" }];
            } else if (min_price != "" && max_price == "") {
                _this.selectShow.priceShow = [{ name: min_price + "万以上" }];
            }
            _this.getSearchList(getPriceLists);
            _this.getList = getPriceList;
            $(".priceBold").css("font-weight", "bold");
        } else if (writeValue) {
            _this.selectShow.brandNameShow = [{ brandGroupName: writeValue }];
            _this.selectShow.theSum = $.extend(
                _this.selectShow.theSum,
                _this.selectShow.brandNameShow[0]
            );
            if (writeValue == "") {
                _this.isShowSomeLable = false;
            } else {
                _this.isShowSomeLable = true;
            }
            _this.getSearchList(brand_name);
            _this.getList = brand_name;
            $(".brandBold").css("font-weight", "bold");
        } else if (typeId.typeIds != undefined) {
            _this.getSearchList(typeId);
            _this.getList = typeId;
            $(".brandBold").css("font-weight", "bold");
        } else if (isNearlyNewCar.isNearlyNewCar == "Y") {
            _this.getSearchList(isNearlyNewCar);
            _this.selectShow.theSum = $.extend(
                _this.selectShow.theSum,
                isNearlyNewCar
            );
            _this.getList = isNearlyNewCar;
            _this.isShowSomeLable = true;
            _this.selectShow.moreShow = [{ name: "准新车" }];
            $(".moreBold").css("font-weight", "bold");
        } else if (isPracticeCar.isPracticeCar == "Y") {
            _this.getSearchList(isPracticeCar);
            _this.selectShow.theSum = $.extend(
                _this.selectShow.theSum,
                isPracticeCar
            );
            _this.getList = isPracticeCar;
            _this.isShowSomeLable = true;
            _this.selectShow.moreShow = [{ name: "练手车" }];
            $(".moreBold").css("font-weight", "bold");
        } else if (brandGroupId.brandGroupIds != undefined) {
            _this.selectShow.brandNameShow = [
                {
                    brandGroupName: brand_group_name,
                    brandGroupIds: brandGroupId.brandGroupIds,
                    city: city,
                    token: token
                }
            ];
            _this.selectShow.theSum = $.extend(
                _this.selectShow.theSum,
                _this.selectShow.brandNameShow[0]
            );
            _this.getSearchList(brandGroupId);
            _this.getList = brandGroupId;
            if (brand_group_name == "") {
                _this.isShowSomeLable = false;
            } else {
                _this.isShowSomeLable = true;
            }
            $(".brandBold").css("font-weight", "bold");
        } else if (typeLevels.typeLevels != undefined) {
            _this.getSearchList(typeLevels);
            _this.getList = typeLevels;
            _this.isShowSomeLable = true;
            _this.selectShow.seachShow = { name: typeLevels.typeLevels };
            $(".brandBold").css("font-weight", "bold");
        } else {
            _this.getSearchList(brand_name);
            _this.getList = brand_name;
        }

        var morefield = [
            "准新车",
            "练手车",
            "微型车",
            "小型车",
            "紧凑型车",
            "中型车",
            "中大型车",
            "跑车",
            "SUV",
            "MPV",
            "豪华车",
            "面包车",
            "皮卡",
            "客车",
            "手动",
            "自动",
            "国一",
            "国二",
            "国三",
            "国四",
            "国五",
            "2座",
            "前驱",
            "后驱",
            "四驱",
            "黑色",
            "白色",
            "灰色",
            "红色",
            "蓝色",
            "金色",
            "银色",
            "黄色",
            "紫色",
            "橙色",
            "棕色",
            "其他"
        ];
        for (var i = 0; i < morefield.length; i++) {
            if (writeValue == morefield[i]) {
                $(".brandBold").css("font-weight", "100");
                $(".moreBold").css("font-weight", "bold");
            }
        }

        if (brandShow) {
            this.brand();
        }

        //获取更多
        $.ajax({
            type: "GET",
            url: this.global.host + "/property/properties",
            data: {},
            success: function(response) {
                if (response.resultCode == "0000") {
                    _this.pTocMore = response.result;
                } else if (response.resultCode == "1111") {
                }
            }
        });
    }
};
</script>



