<template>
  <div class="listtemplates" v-if="recommendData.length > 0">
    <template v-if="recommendData.length > 0">
      <p class="recommend_title">为您推荐</p>
    </template>
    <ul>
      <li v-for="(item,index) in recommendData" @click="saveHistory(item)">
        <router-link :to="{name: 'detail' , params:{id: item.code}}">
          <img class="car" :src="item.firstImage" v-if="item.firstImage != null"/>
          <img  class="car" src="../../../assets/noImg.jpg" alt="" v-else>
        </router-link>
        <div class="car_mess">
          <div class="pub_height">
            <router-link :to="{name: 'detail' , params:{id: item.code}}">
              <p class="car_name">{{item.title}}</p>
            </router-link>
            <p class="car_time">
              <template v-if="item.firstRegisterDate">
                <span class="listTime">{{item.firstRegisterDate | listTime}}</span>
              </template>
              <template v-if="item.firstRegisterDate && item.mileage">
                /
              </template>
              <template v-if="item.mileage">
                <span class="mileage">{{item.mileage}} 万公里</span>
              </template>
              <template v-if="item.mileage && item.exhibitionArea">
                /
              </template>
              <template v-if="item.exhibitionArea">
                /{{item.exhibitionArea}}区{{item.exhibitionCode}}
              </template>
            </p>
          </div>

          <div class="price_border">
            <span class="show_price">{{item.originalPrice}}万</span>
            <span v-for="tag in item.tags" class="tag" :class="{hand : tag == '练手', suv : tag == 'SUV', beNew : tag == '准新'}">{{tag}}</span>
            <template v-if="item.isInLook == 0">
              <span :id="'to_look' + index" class="to_look" @click="addCarList(item)"></span>
            </template>
            <template v-else="item.isInLook == 1">
              <span class="looked" @click="removeCarList(item)"></span>
            </template>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<style lang="scss" scoped>
.listtemplates {
    width: 100%;
    height: auto;
    border-top: 10px solid #fff;
    border-top-left-radius: 16px;
    border-top-right-radius: 16px;
    margin-top: -8px;
    box-shadow: 0px 5px 15px #656161;
    .recommend_title {
        font-size: .3rem;
        color: #191919;
        border-left: 6px solid #F6A32B;
        margin: .18rem 0 .3rem .24rem;
        padding-left: .14rem;
        height: .4rem;
        line-height: .4rem;
    }
    .loading {
        margin-top:40%;
    }
    ul {
        margin-top: .3rem;
        li {
            display: flex;
            border-bottom: 1px solid #f2f2f2;
            padding-bottom: .2rem;
            &:not(:first-child) {
                margin-top:.3rem;
            }
            .car {
                width: 2.20rem;
                height: 1.64rem;
                margin-left: .24rem;
            }
            .car_mess {
                margin-left: .22rem;
                height:1.64rem;
                position: relative;
                a {
                    text-decoration: none;
                    display: block;
                }
                .pub_height {
                    height: 1.26rem;
                }
                .car_name {
                    color: #222;
                    width: 4.54rem;
                    font-size: .26rem;
                    font-weight: bold;
                    line-height: .4rem;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                }
                .car_time {
                    margin: .08rem 0 .16rem 0;
                    height: .3rem;
                    span {
                        font-size: .22rem;
                        color:#666;
                    }
                }
                .price_border {
                    display: flex;
                    line-height:1;
                    margin-top: .1rem;
                    .tag {
                        display: inline-block;
                        font-size: .22rem;
                        border-radius: .04rem;
                        width: .7rem;
                        height: .28rem;
                        line-height: .28rem;
                        text-align: center;
                    }
                    span:not(:first-child) {
                        margin-left:.1rem;
                    }
                    .hand {
                        border:1px solid #696969;
                        color: #696969;
                    }
                    .beNew {
                        border:1px solid #ffb804;
                        color: #ffb804;
                    }
                    .suv {
                        border:1px solid #6196fc;
                        color: #6196fc;
                    }
                    .show_price {
                        float: left;
                        font-size: .3rem;
                        color: #ff0000;
                    }
                    .to_look {
                        position: absolute;
                        right: 0;
                        bottom:0;
                        width: .44rem;
                        height: .5rem;
                        background: url('../../../assets/love.png') no-repeat;
                        background-size: .38rem;
                    }
                    .looked {
                        position: absolute;
                        right: 0;
                        bottom:0;
                        width: .44rem;
                        height: .5rem;
                        background: url('../../../assets/loved.png') no-repeat;
                        background-size: .38rem;
                    }
                }
            }
        }
    }
    #deleteInfo {
        width: 50%;
        height: 0.7rem;
        line-height: 0.7rem;
        padding: 0.1px 0;
        text-align: center;
        background-color: #000;
        opacity: 0.8;
        font-size: 0.3rem;
        color: #ffffff;
        position: fixed;
        left: 25%;
        top: 30%;
        border-radius: 0.4rem;
    }
}
</style>

<script>
import Vue from "vue";
import { Toast } from 'mint-ui';
export default {
  props:{
		recommendData: {}
	},
  data(){
    return {
      s: [],
      customerId: '',
      tokens: '',
      nativeHref: ''
    }
  },
  mounted(){
    //this.nativeHref = window.location.href;
    if(window.localStorage.getItem("user")){
      this.customerId = JSON.parse(window.localStorage.getItem("user")).customer;
      this.tokens = JSON.parse(window.localStorage.getItem("user")).token;
      if(window.localStorage.getItem("item")){
        this.addCarList(JSON.parse(window.localStorage.getItem("item")));
        window.localStorage.setItem("item","");
      }
    }
  },
  methods: {
    addCarList: function (item) {
      var _this = this;
      if(item.is_in_look == 1){
          return;
      }else{
        if (window.localStorage.getItem("user")) {
          $.ajax({
            type: "post", url: _this.global.host + "/watchList/add",
            data: {
              customerId: JSON.parse(window.localStorage.getItem("user")).customer,
              token: JSON.parse(window.localStorage.getItem("user")).token,
              vehicleId: item.code
            },
            success: function (response) {
              if (response.resultCode == '0000') {
                item.is_in_look = 1;
                Toast("加入看车单成功");
              } else if (response.resultCode == '1111') {
                Toast("加入看车单失败");
              }
            }
          });
        }else{
          window.localStorage.setItem("item",JSON.stringify(item))
          _this.$router.push('/login');
        }
      }
    },
    removeCarList(item){
      var _this = this;
      $.ajax({
        type: "POST", url: this.global.host + 'watchList/remove',
        data: {
          customerId: _this.customerId,
          token: _this.tokens,
          vehicleId: item.code,
        },
        success: function (response) {
          if (response.resultCode === '0000') {
            Toast("取消加入看车单");
            item.is_in_look = 0;
          } else if (response.resultCode === '1111') {
            Toast("操作失败")
          }
        }
      });
    },
    saveHistory(item){
      var Storage = window.localStorage;
      var ifHas = Storage.getItem("History");
      if (ifHas) {
        var newStorage = JSON.parse(ifHas);
        if (newStorage.length == 50) {
          newStorage.pop();
          newStorage.unshift(item);
          var arrTable = {},arrData = [];
            for ( var i = 0; i < newStorage.length; i++ ){
                if( !arrTable[newStorage[i].code]){
                    arrTable[newStorage[i].code] = true;
                    arrData.push(newStorage[i]);
                }
            }
            Storage.setItem("History", JSON.stringify(arrData));
        } else {
          newStorage.unshift(item)
          var arrTable = {},arrData = [];
            for ( var i = 0; i < newStorage.length; i++ ){
                if( !arrTable[newStorage[i].code]){
                    arrTable[newStorage[i].code] = true;
                    arrData.push(newStorage[i]);
                }
            }
            Storage.setItem("History", JSON.stringify(arrData));
        }
      } else {
        this.s.unshift(item)
        Storage.setItem("History", JSON.stringify(this.s));
      }
    }
  }
}
Vue.filter('listTime', function (value) {//value为13位的时间戳
  function addt(m) {
    return m < 10 ? '0' + m : m
  }
  var time = new Date(parseInt(value));
  var y = time.getFullYear();
  var m = time.getMonth() + 1;
  var d = time.getDate();

  return y + '年' + addt(m) + '月';
});
</script>



